import React, { PureComponent } from "react";
import AudioPlayer from 'react-h5-audio-player';
import 'react-h5-audio-player/lib/styles.css';

export default class PopUpAudio extends PureComponent {
    constructor(props) {
        super(props);
    }

    render() {
        const { data } = this.props;
        return <div className="tb_post_modal_media_holder">
                <div className="tb_post_modal_audio_thumb_blur" style={{ backgroundImage: `url(${data.postFileNew})`}}><div></div></div>
                <div className="tb_post_modal_audio_thumb" style={{ backgroundImage: `url(${data.postFileNew})`}}><div></div></div>
                <div className="tb_post_modal_audio_wrapper"> 
                    <AudioPlayer
                        className="tb_post_modal_audio"
                        autoPlay
                        showJumpControls={false}
                        showDownloadProgress={false}
                        showFilledProgress={false}
                        hasDefaultKeyBindings={false}
                        layout='horizontal-reverse'
                        customVolumeControls={[]}
                        customAdditionalControls={[]}
                        src={data.mediaUrl}/>
                </div> 
            </div>
    }
}